import StatisticList from './statisticList';

export default function Statistic ({items}) {
	return (
		<section className="statistics">
			<h2 className="title">Upload stats</h2>

			<ul className="stat-list">
				{items.map(item => 
					<StatisticList	
						key={item.id}						
						label = {item.label}
						percentage = {item.percentage}
					/>)	
				}
			</ul>		
		</section>
	)
}



