import FriendsList from './friendsList';

export default function Friends ({items}) {
	return (
		<ul className="friend-list">
				{items.map(item => 
					<FriendsList	
						key={item.id}						
						avatar = {item.avatar}
						name = {item.name}
						isOnline = {item.isOnline}
					/>)	
				}
		</ul>
	)
}